import React from 'react';
import _get from 'lodash/get';
import { LOCATE, CURRENCY } from 'configs/locate';

export const renderPrice = prop => {
  const value = _get(prop, 'value', '');
  return `${
    value
      ? new Intl.NumberFormat(LOCATE, {
          style: 'currency',
          currency: CURRENCY,
        }).format(value)
      : '-'
  }`;
};

export const renderItem = ({ value }) => <span>{value || '-'}</span>;
