// @flow
import React, { Fragment, memo } from 'react';
import type { Element, AbstractComponent } from 'react';
import Table from 'components/Table';
import Loader from 'components/Loader';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import type { Offer } from 'types/Offer';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import {
  mappingOfferContingencies,
  isOfferContingencies,
  canRemoveContingency,
} from 'helpers/Offer';
import { renderItem } from 'helpers/Table';
import ActionButton from './ActionButton';

type Props = {
  offers: Array<Offer>,
  isLoading: boolean,
  isBuyer?: boolean,
};

const checkProps = ({ offers }) => offers && offers.length;

const ContingencyList = (
  props: Props,
): Element<'div' | typeof Loader | typeof Fragment> => {
  const { offers, isLoading, isBuyer } = props;

  const renderAction = ({ value }) =>
    value && canRemoveContingency(value) ? <ActionButton offer={value} /> : '-';

  const sellerColumns = [
    {
      Header: 'Type',
      accessor: 'title',
      Cell: renderItem,
    },
    {
      Header: 'Due Date',
      accessor: 'dueDate',
      Cell: renderItem,
    },
    {
      Header: 'Status',
      accessor: 'status',
      Cell: renderItem,
    },
  ];

  const buyerColumns = [
    ...sellerColumns,
    {
      Header: 'Action',
      accessor: 'offer',
      Cell: renderAction,
    },
  ];

  if (isLoading) return <Loader />;

  const listTables = offers.reduce((acc, offer) => {
    if (
      isOfferContingencies(offer) &&
      !_isEmpty(_get(offer, ['Contingencies']))
    ) {
      const contingencies = mappingOfferContingencies(offer);

      return [
        ...acc,
        <Table
          key={_get(offer, ['OfferId'])}
          columns={isBuyer ? buyerColumns : sellerColumns}
          data={contingencies}
        />,
      ];
    }

    return acc;
  }, []);

  return listTables.length ? (
    <div className="offer_block">
      <div className="offer_container">{listTables}</div>
    </div>
  ) : (
    <></>
  );
};

export default (hideIfInvalidProps(checkProps)(
  memo(ContingencyList),
): AbstractComponent<Props>);
