// @flow
import React, { useState, Fragment } from 'react';
import type { Element } from 'react';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import _get from 'lodash/get';
import { APP_BASE_URL, FORM_TYPE_IDS, OFFER_STEPS } from 'shared/Consts';
import type { Offer } from 'types/Offer';

type Props = {
  offer: Offer,
};

const ActionButton = ({ offer }: Props): Element<typeof Fragment> => {
  const [anchorEl, setAnchorEl] = useState(null);
  const offerId = _get(offer, ['OfferId']);

  const removalForm = _get(offer, ['Forms']).find(
    form => _get(form, ['FormTypeId']) === FORM_TYPE_IDS.contingencyRemoval,
  );
  const formId = _get(removalForm, ['FormId']);

  const onClickOpenMenu = event => {
    setAnchorEl(event.currentTarget);
  };

  const onClickCloseMenu = () => {
    setAnchorEl(null);
  };

  const isOpen = Boolean(anchorEl);
  const redirectUrl = `${APP_BASE_URL}/offer?offerId=${offerId}&step=${OFFER_STEPS.confirmTransfer}&formId=${formId}`;

  return (
    <>
      <button
        type="button"
        className="minimal-btn animated_fastest"
        onClick={onClickOpenMenu}
      >
        <i className="fa fa-ellipsis-h"></i>
      </button>
      <Menu
        anchorEl={anchorEl}
        keepMounted
        open={isOpen}
        onClose={onClickCloseMenu}
      >
        <MenuItem component="a" href={redirectUrl}>
          Remove Contingencies
        </MenuItem>
      </Menu>
    </>
  );
};

export default ActionButton;
